* {
    font-family: Helvetica, Arial;
}

img{ display: inline}

body {
    padding: 60px;
}

input {
    color: gray;
    font-size: 1.2rem;
    width: 100%;
}

.class-grid {
    display: flex;
    flex-grow: 1;
    flex-wrap: wrap;
}

.card {
    display: flex;
    flex-direction: column;
    align-content: center;
    border: 1px solid #ccc;
    border-radius: 6px;
    overflow: hidden;
    margin-bottom: 10px;
    margin-right: 10px;
    max-width: 200px;
}

.card p {
    display: flex;

    padding: 5px;
    text-align: center;
}

.card img{
    display: flex;

    max-height: 250px;
}

